<template>
  <div>
    <div>{{message}}</div>
  </div>
</template>

<script>
export default {
  name: 'error',
  data() {
    return {
      message: ''
    };
  },
  mounted() {
    let { error = '' } = this.$route.query;
    this.message = error;
  }
};
</script>

<style>

</style>



